
.tips{
  position: fixed;
  z-index: 999999;
  background:rgba(0,0,0,.8);
  color: #fff;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
     -moz-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
       -o-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  padding: 10px 20px;
  -moz-border-radius: 6px;
       border-radius: 6px;
}



