
body {
  background: #1b1b1b!important;
}
.player {
  width: 100%;
  padding-bottom: 1.13rem;
}
.player .goback-nav {
  width: 100%;
  background: #000;
  height: .88rem;
  display: block;
  text-align: center;
  color: #878787;
  font-size: .26rem;
  line-height: .88rem;
  position: relative;
}
.player .goback-nav img {
  width: .14rem;
  position: absolute;
  top: 50%;
  left: .38rem;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
       -o-transform: translateY(-50%);
          transform: translateY(-50%);
}
.player .mobile {
  width: 100%;
}
.player .mobile .player-box {
  width: 100%;
  background: #000;
  position: relative;
  min-height: 3.8rem;
}
.player .mobile .player-box .iframe-box {
  width: 100%;
  height: 4.20rem;
  overflow: hidden;
}
.player .mobile .player-box .player-wrap {
  width: 100%;
  height: 3.8rem;
  color: #fff;
  overflow: hidden;
}
.player .mobile .player-box .no-line {
  display: block;
  width: 100%;
  color: rgba(255, 255, 255, 0.8);
  font-size: .32rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.player .mobile .player-box .poster {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 9999;
  cursor: pointer;
}
.player .mobile .player-box .poster img {
  display: block;
  width: 100%;
}
.player .mobile .errBox {
  width: 100%;
  background: #000;
  color: #fff;
  text-align: center;
  height: .64rem;
  line-height: .64rem;
  cursor: pointer;
}
.player .mobile .line-box {
  width: 100%;
  z-index: 9999;
  position: relative;
}
.player .mobile .line-box .line-title {
  width: 100%;
  height: .74rem;
  line-height: .74rem;
  color: #fff;
  text-align: left;
  font-size: .26rem;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  padding-left: .34rem;
  position: relative;
}
.player .mobile .line-box .line-title .title-tips {
  font-size: .2rem;
  float: right;
  padding-right: .34rem;
  color: #666;
}
.player .mobile .line-box .line-content {
  z-index: 1;
  width: 100%;
  background: #2d2d2d;
  text-align: left;
  font-size: .24rem;
  color: #fff;
}
.player .mobile .line-box .line-content .line-item,
.player .mobile .line-box .line-content a {
  width: 100%;
  height: .96rem;
  line-height: .96rem;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  padding: 0 .34rem;
  position: relative;
  color: #fff;
  font-size: .24rem;
}
.player .mobile .line-box .line-content .line-item:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #1d1d1d;
  bottom: 0;
  left: 0;
  position: absolute;
}
.showline-enter-active,
.showline-leave-active {
  /*transform: translateY(0);*/
  opacity: 1;
  -o-transition: all .5s;
  -moz-transition: all .5s;
  transition: all .5s;
}
.showline-enter,
.showline-leave-to {
  /*transform: translateY(-100%);*/
  opacity: 0;
}
.line-content .line-item.active {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  background: #3b3b3b;
  -moz-box-shadow: 1px 0px .05rem #999 inset;
       box-shadow: 1px 0px .05rem #999 inset;
}



