
.listBan{
  width: 100%;
  min-height: .64rem;
  text-align: center;
  line-height: .64rem;
  position: relative;
  z-index: 200;
}
.listBan a{
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top:0;
  left: 0;
}
.listBan img{
  display: block;
  width: 100%;
  border:none;
}



